import React from 'react'
import { Helmet } from 'react-helmet';
import { Container, Row, Col } from 'react-bootstrap';
import '../../App.css';
import YouTube from 'react-youtube';

function About() {
    const opts = {
      height: '400',
      width: '640',
      playerVars: {
        // https://developers.google.com/youtube/player_parameters
        modestbranding: 1,
        autoplay: 0,
      },
    };
    return (
      <>
        <Helmet>
          <title>About Us | Ngee Ann Polytechnic</title>
        </Helmet>
        <Container fluid className="header-wrap">
          <Row className="about-header-banner-logo">
            <Container className="about-header-banner-text">
              <h1>About Us</h1>
            </Container>
          </Row>
          <Row className="about-us-body">
            <Col xs={12} md={12} className="tour">
              <h2>Virtual Tour</h2>
              <Container className="tour-container">
                <YouTube videoId="yfh5CSL_ETI" className="responsive-iframe" containerClassName="video" opts={opts} />
              </Container>
            </Col>
            <Col xs={12} md={12} className="welcome">
              <Col xs={12} md={5} className="welcome-circle-container">
                <div className="circle raymond-lam-sk-circle"></div>
                <h5>Mr. Raymond Lam S.K</h5>
                <h6>Senior Lecturer School of Engineering</h6>
              </Col>
              <Col xs={12} md={7} className="welcome-container">
                <h2>Welcome to our website</h2>
                <p>
                  Welcome to the School of Engineering or SoE! This is a Final Year Project that has been undertaken by 3 of our aspiring Final Year students from their respective diploma programmes offered here in the School of Engineering. Mr. Raymond Lam S.K is our supervisor in charge of overseeing this final year project. We hope that you have enjoyed our website and have learnt more about SoE. We also warmly welcome you to join us in our virtual open house from 7 to 9 January 2021.
                </p>
              </Col>
            </Col>
            <Col xs={12} md={12} className="team">
              <h2>Meet The Team</h2>
              <Container className="team-container">
                <Container className="nakagawa-miho-sherry-container">
                  <Container className="team-circle">
                    <div className="circle nakagawa-miho-sherry-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Nakagawa Miho Sherry</h3>
                    <h6>Diploma in Electronics and Computer Engineering</h6>
                    <p>Responsibilities stuff..</p>
                  </Container>
                </Container>
                <Container className="koh-jun-xian-jaymeson-container">
                  <Container className="team-circle">
                    <div className="circle koh-jun-xian-jaymeson-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Koh Jun Xian Jaymeson</h3>
                    <h6>Diploma in Aerospace Electronics with Minor in Business Management</h6>
                    <h7>Developed Landing Page, Header, Footer, Student Life, About webpages</h7>
                    <p>Loves fiddling with code in his free time and exploring the unknowns to pique his curiousity</p>
                  </Container>
                </Container>
                <Container className="kugan-elamaran-container">
                  <Container className="team-circle">
                    <div className="circle kugan-elamaran-circle"></div>
                  </Container>
                  <Container className="team-content">
                    <h3>Kugan S/O Elamaran</h3>
                    <h6>Diploma in Electronics and Computer Engineering</h6>
                    <p>Responsibilities stuff..</p>
                  </Container>
                </Container>
              </Container>
            </Col>
          </Row>
        </Container>
      </>
    )
}

export default About
